<template>
	<v-layout class="ma-4" justify-center>
		<message-dialog
			v-model="message"
			title="Error"
			:message="text" />
		<v-layout v-if="userInfo" column justify-center reverse wrap>
			<v-flex class="ma-2" md12 lg8 text-xs-center justify-center>
				<img :src="sourceUrl" alt="No video source available" />
			</v-flex>
			<v-flex class="ma-2" md12 lg4 text-xs-center justify-center>
				<v-card>
					<v-toolbar dark class="primary">
						<v-card-title class="headline">
							Video source
						</v-card-title>
					</v-toolbar>
					<v-card-text class="pa-4">
						<v-select
							:items="items"
							v-model="source" />
						<v-form v-if="source === 'RTSP'">
							<v-text-field
								v-model="ip"
								type="text"
								label="IP Address"
								clearable />
							<v-text-field
								v-model="port"
								type="text"
								label="Port Number"
								clearable />
							<v-text-field
								v-model="username"
								type="text"
								label="Username"
								clearable />
							<v-text-field
								v-model="password"
								type="password"
								label="Password"
								clearable />
						</v-form>
						<v-btn class="primary" dark @click="connect">
							Connect
						</v-btn>
					</v-card-text>
				</v-card>
			</v-flex>
		</v-layout>
		<v-card style="width: 50%" v-else>
			<v-card-title class="headline">
				Please login first.
			</v-card-title>
			<v-card-text class="subheading">
				This monitoring system is only accessible
				to registered users.
			</v-card-text>
			<v-card-actions>
				<v-btn dark color="green" type="submit" @click="setLoginDialog(true)">
					Log in
				</v-btn>
			</v-card-actions>
		</v-card>
	</v-layout>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import MessageDialog from '@/components/MessageDialog.vue';
import axios from 'axios';

export default {
	components: {
		'message-dialog': MessageDialog
	},
	data()
	{
		return {
			message: false,
			text: '',
			source: 'System',
			sourceUrl: '/api/video/system',
			items: [
				'System',
				'RTSP'
			],
			ip: '',
			port: '',
			username: '',
			password: ''
		};
	},
	computed: {
		...mapState(['userInfo']),
	},
	methods: {
		...mapMutations(['setLoginDialog']),
		async connect()
		{
			if(this.source === 'RTSP')
			{
				const res = (await axios.post('/api/video/rtsp', {
					ip: this.ip,
					port: this.port,
					username: this.username,
					password: this.password
				})).data;
				if(!res.success)
				{
					this.message = true;
					this.text = res.data;
					return;
				}
			}
			this.sourceUrl =  '/api/video/' + this.source.toLowerCase();
		}
	}
};
</script>
